import $ from "jquery";
import express from "express";

const app= express();
const file = 'home.html'; // Passe ici un chemin absolu vers le point d'entre
const options = {}; // Voir la section des options de la doc de l'api, pour les possibilits

const bundler = new Bundler(file, options);

app.use(bundler.middleware());

app.get('/login', function(req, res) {
  res.render('index');
});

app.get('/', function(req, res) {
  res.render('home');
});

app.get('*', function(req, res) {
  res.render('home');
});
